import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { DropComponentData } from './drop-component-data.class';
import { IViewComponentDropData, IViewDesignerCanvasLayout } from './interfaces/view-designer-canvas.interfaces';
import { ViewDesignerCanvasService } from './view-designer-canvas.service';

@Component({
  selector: 'rx-view-designer-canvas',
  templateUrl: './view-designer-canvas.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [ViewDesignerCanvasService]
})
export class ViewDesignerCanvasComponent implements OnInit {
  @Input()
  layout: IViewDesignerCanvasLayout;

  @Input()
  isReadOnly: boolean;

  @Output()
  componentSelect: EventEmitter<any> = new EventEmitter();

  @Output()
  componentDrop: EventEmitter<DropComponentData> = new EventEmitter<DropComponentData>();

  @Output()
  componentRemove: EventEmitter<string> = new EventEmitter<string>();

  @Output()
  beforeComponentDropInRoot = new EventEmitter<IViewComponentDropData>();

  constructor(protected viewDesignerCanvasService: ViewDesignerCanvasService) {}

  ngOnInit(): void {
    this.viewDesignerCanvasService.componentSelect$.subscribe((event) => {
      this.componentSelect.emit(event);
    });

    this.viewDesignerCanvasService.componentRemove$.subscribe((event) => {
      this.componentRemove.emit(event);
    });

    this.viewDesignerCanvasService.componentDrop$.subscribe((event) => {
      this.componentDrop.emit(event);
    });
  }
}
